<template>
  <div>
    <span v-if="message">{{ message }}</span>
  </div>
</template>

<script>
import axios from "axios";
import FingerprintUtil from "@/module/FingerprintUtil";

export default {
  name: "Access",
  data() {
    return {
      platform: "",
      linkContent: "",
      message: "",
    };
  },
  created() {
    this.platform = FingerprintUtil.platform();
  },
  mounted() {
    this.getIpAddress()
      .then((ip) => {
        const result = {};
        // if (ip) {
        //   result.ip = ip;
        // }
        const fp = FingerprintUtil.get();
        if (fp.os && fp.os.name) {
          result.os = { name: fp.os.name };
        }
        // if (fp.device) {
        //   result.device = fp.device;
        // }

        this.createBrowserFingerprint(result).then((hash) => {
          this.message = hash;
          const res = this.mockDbQuery(this.message);
          this.toAppScript(res);
        });
      })
      .catch((error) => {
        console.error("Error getting IP: ", error);
        // Handle the error
      });
  },
  methods: {
    toAppScript(content) {
      if (this.platform === "ios") {
        if (window.webkit) {
          window.webkit.messageHandler.getAppLink.postMessage(content);
        }
      }
      if (this.platform === "android") {
        if (window.Android) {
          window.Android.getAppLink(content);
        }
      }
    },
    mockDbQuery(key) {
      const pool = {
        // {"agent":{"os":{"name":"iOS"}},"hash":"3e23e182f6610757be5a6fdb81ed150362a573f13ca3a22c79f19ac50b3ae3c2"}
        "3e23e182f6610757be5a6fdb81ed150362a573f13ca3a22c79f19ac50b3ae3c2":
          "/pageA",
        // {"agent":{"os":{"name":"Android"}},"hash":"1b1bbc2aa37f17be15edbe657b3657b0f9912cec509ac922a3ddeeb1445d8ef4"}
        "1b1bbc2aa37f17be15edbe657b3657b0f9912cec509ac922a3ddeeb1445d8ef4":
          "/pageB",
      };
      return pool[key] || "";
    },
    async getIpAddress() {
      try {
        const response = await axios.get("https://api.ipify.org?format=json");
        return response.data.ip;
      } catch (error) {
        console.error("Error: ", error);
        throw error; // Propagate the error
      }
    },
    async createBrowserFingerprint(browserInfo) {
      // 將瀏覽器資訊轉換為 JSON 字串
      const input = JSON.stringify(browserInfo);

      const textAsBuffer = new TextEncoder().encode(input);
      const hashBuffer = await window.crypto.subtle.digest(
        "SHA-256",
        textAsBuffer
      );
      const hashArray = Array.from(new Uint8Array(hashBuffer));
      const hash = hashArray
        .map((item) => item.toString(16).padStart(2, "0"))
        .join("");
      return hash;
    },
  },
};
</script>
